	getImageList: function (views) {
	    const imageList = [];
	    for (let i = 0; i < views.length; i++) {
	        if (views[i].type === 'image') {
	            imageList.push(views[i].url);
	        }
	    }
	    this.imageList = imageList;
		this.downLoadImages(0);
	},
	downLoadImages: function (index) {
	    const imageList = this.imageList;
	    const tempFileList = this.tempFileList;
	    if (index < imageList.length) {
	        // 
	        this.getImageInfo(imageList[index]).then(imgInfo => {
				if(imgInfo.url) {
				
				} 
				tempFileList.push(imgInfo)
				console.log('imgInfo:',imgInfo)
				this.tempFileList = tempFileList;
				this.downLoadImages(index + 1);
	        })
	    } else {
			console.log('tempFileList:',tempFileList)
	        this.startPainting();
	    }
	},
	tailorImageDraw: function (view, imgInfo) {
	    // _views.tailor == 'center'
	    let px = 1;
	    let wMultiple = (view.width * px) / imgInfo.originWidth;
	    let hMultiple = (view.height * px) / imgInfo.originHeight;
	    let sizeNormal = false;
	    if (wMultiple <= 1 && hMultiple <= 1) {
	        let multiple = wMultiple > hMultiple ? wMultiple : hMultiple;
	        // let tempW = imgInfo.originWidth * multiple;
	        // let tempH = imgInfo.originHeight * multiple;
	        let tempW = (view.width * px) / multiple;
	        let tempH = (view.height * px) / multiple;
	        let sx = (imgInfo.originWidth - tempW) / 2;
	        let sy = (imgInfo.originHeight - tempH) / 2;
	        let ex = sx + tempW;
	        let ey = sy + tempH;
	        view['sx'] = sx || 0;
	        view['sy'] = sy || 0;
	        view['ex'] = ex || 0;
	        view['ey'] = ey || 0;
	        sizeNormal = true;
	    }
	    const data = {
	        ...view,
	        ow: imgInfo.originWidth,
	        oh: imgInfo.originHeight
	    };
	    // console.log(JSON.stringify(data));
	    if (!sizeNormal) {
	        delete data.tailor;
	    }
	    // this.drawImage(data);
	    return data;
	},
 
	startPainting: function () {
	    const { tempFileList, paintingData: { views } } = this;
	    for (let i = 0, imageIndex = 0; i < views.length; i++) {
	        if (views[i].type === 'image') {
	            let _img = tempFileList[imageIndex];
	            let _views = views[i];
	            let drawData = {};
	            if (_views.tailor) {
	                _views = this.tailorImageDraw(_views, _img);
	            }
				if(_img) _views['url'] = _img.localPath ;
	            drawData = {
	                ..._views,
	               // url: _img.localPath
	            };
				if(_views['url']) this.drawImage(drawData);
	            // if (_views.radius) {
	            //     this.drawRoundRect(drawData);
	            // }
	            // else {
	            // }
	            imageIndex++;
				
	        } else if (views[i].type === 'text') {
				 this.drawText(views[i]);
	        } else if (views[i].type === 'rect') {
	            this.drawRect(views[i]);
	        }
	        else if (views[i].type === 'round') {
	            this.drawRound(views[i]);
	        }
	    }
		 
	    this.ctx.draw(true, () => {
	        uni.setStorageSync('CACHE_KEYS', this.cache);
	        this.saveImageToLocal();
	    })
	 
	},
	 
	_drawRadiusRect: function (params) {
	    const {
	        top = 0,
	        left = 0,
	        width = 0,
	        height = 0,
	        radius = 0,
	     } = params;
	    let x = left;
	    let y = top;
	    let w = width;
	    let h = height;
	    // let bgc = background;
	    let r = radius;
	    // let br = radius;
	    this.ctx.beginPath();
	    this.ctx.moveTo(x + r, y);    // 
	    this.ctx.lineTo(x + w - r, y);
	    this.ctx.arc(x + w - r, y + r, r, 2 * Math.PI * (3 / 4), 2 * Math.PI * (4 / 4));
	    this.ctx.lineTo(x + w, y + h - r);
	    this.ctx.arc(x + w - r, y + h - r, r, 0, 2 * Math.PI * (1 / 4));
	    this.ctx.lineTo(x + r, y + h);
	    this.ctx.arc(x + r, y + h - r, r, 2 * Math.PI * (1 / 4), 2 * Math.PI * (2 / 4));
	    this.ctx.lineTo((x), (y + r));
	    this.ctx.arc(x + r, y + r, r, 2 * Math.PI * (2 / 4), 2 * Math.PI * (3 / 4));
	
	    // this.ctx.moveTo(x + r, y);
	    // this.ctx.arcTo(x + w, y, x + w, y + h, r);
	    // this.ctx.arcTo(x + w, y + h, x, y + h, r);
	    // this.ctx.arcTo(x, y + h, x, y, r);
	    // this.ctx.arcTo(x, y, x + w, y, r);
	},
	drawImage: function (params) {
	    const {
	        type = '',
	        background,
	        top = 0,
	        left = 0,
	        width = 0,
	        height = 0,
	        radius = 0,
	        url = '',
	        sx = 0,
	        sy = 0,
	        ex = 0,
	        ey = 0
	    } = params;
		//console.log('wishshare drawImage params:',params);
		 
	    let x = left;
	    let y = top;
	    let w = width;
	    let h = height;
	    let r = radius;
	    this.ctx.save();
	    if (radius) {
	        this.ctx.beginPath();
	        
	
	        // if (radius === parseInt(width / 2)) {
	        //     console.log('');
	        //     this.ctx.beginPath();
	        //     this.ctx.arc(left + radius, top + radius, radius, 0, 2 * Math.PI);
	        //     this.ctx.setFillStyle(params.background || '#ffffff')
	        //     this.ctx.fill()
	        //     this.ctx.clip();
	        //     this.ctx.drawImage(url, x, y, w, h);
	        // }
	        // else {
	        // }
	        this._drawRadiusRect(params);
	        this.ctx.fill();
	        this.ctx.clip();
	    }
	    if ('tailor' in params) {
			if(url){
				 this.ctx.drawImage(url, sx, sy, ex, ey, left, top, width, height);
			}else{
				uni.showModal({
				    title: 'tailor',
				    content: 'draw url:'+ url
				}) 
			}
	    } else {
			if(url){
				 this.ctx.drawImage(url, left, top, width, height);
				 console.log('wishshare drawImage url:',url, left, top, width, height);
			}else{
				uni.showModal({
				    title: 'drawImage ',
				    content: 'draw url:'+ url
				}) 
			}
	       
	    }
	    // this.drawImage(params);
	    
	    this.ctx.restore();
	},
	
	drawText: function (params) {
	    let {
	        MaxLineNumber = 2,
	        breakWord = false,
	        color = 'black',
	        content = '',
	        fontSize = 16,
	        top = 0,
	        left = 0,
	        lineHeight = 20,
	        textAlign = 'left',
	        width,
	        bolder = false,
	        textDecoration = 'none'
	    } = params;
	    if (bolder) {
	        top -= 0.3;
	    }
	    // this.ctx.save();
	    // this.ctx.beginPath();
	    // this.ctx.stroke();
	    let _setStyle = () => {
	        // this.ctx.save();
	        this.ctx.closePath();
	        this.ctx.beginPath();
	        this.ctx.setTextBaseline('top');
	        this.ctx.setFillStyle(color);
	        this.ctx.setFontSize(fontSize);
	        this.ctx.setTextAlign(textAlign);
	    }
	    _setStyle();
	    if (!breakWord) {
	        this.ctx.fillText(content, left, top);
	        this.drawTextLine(left, top, textDecoration, color, fontSize, content);
	    } else {
	        let fillText = '';
	        let fillTop = top;
	        let lineNum = 1;
			let len = 0;  
	        for (let i = 0; i < content.length; i++) {
				len += content[i].match(/[^\x00-\xff]/ig)?2:1   //
	            fillText += [content[i]];
	            // _setStyle();
	            if (this.ctx.measureText(fillText).width> width || (len+3)*uni.upx2px(fontSize) > width) { //uni-app APPmeasureText bug 
	                if (lineNum === MaxLineNumber) {
	                    if (i !== content.length) {
	                        fillText = fillText.substring(0, fillText.length - 1) + '...';
	                        // _setStyle();
	                        this.ctx.fillText(fillText, left, fillTop);
	                        this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText);
	                        fillText = '';
	                        break;
	                    }
	                }
	                // _setStyle();
	                this.ctx.fillText(fillText, left, fillTop);
	                this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText);
	                fillText = '';
					len = 0 ;
	                fillTop += lineHeight;
	                lineNum++;
	            }
	        }
	        // _setStyle();
	        this.ctx.fillText(fillText, left, fillTop);
	        this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText);
	    }
	    // this.ctx.draw();
	    if (bolder) {
	        this.drawText({
	            ...params,
	            left: left - 0.3,
	            top: top,
	            bolder: false,
	            textDecoration: 'none'
	        });
	    }
	},
	drawTextLine: function (left, top, textDecoration, color, fontSize, content) {
	    if (textDecoration === 'underline') {
	        this.drawRect({
	            background: color,
	            top: top + fontSize * 1.2,
	            left: left - 1,
	            width: this.ctx.measureText(content).width + 2,
	            height: 1
	        });
	    } else if (textDecoration === 'line-through') {
	        this.drawRect({
	            background: color,
	            top: top + fontSize * 0.6,
	            left: left - 1,
	            width: this.ctx.measureText(content).width + 2,
	            height: 1
	        });
	    }
	},
	drawRect: function (params) {
	    // console.log(params)
	    const { background, top = 0, left = 0, width = 0, height = 0 } = params
	    this.ctx.save();
	    this.ctx.setFillStyle(background);
	    if (params.radius) {
	        this._drawRadiusRect(params);
	        this.ctx.fill();
	    }
	    else {
	        this.ctx.setFillStyle(background);
	        this.ctx.fillRect(left, top, width, height);
	    }
	    this.ctx.restore();
	},
	getImageInfo: function (url) {
	    return new Promise((resolve, reject) => {
	        /*  */
	        if (this.cache[url]) {
	            resolve(this.cache[url]);
	        } else {
	            const objExp = new RegExp(/^http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- .\/?%&=]*)?/);
	            if (objExp.test(url)) {
					
	                uni.getImageInfo({
	                    src: url,
	                    complete: res => {
	                        if (res.errMsg === 'getImageInfo:ok') {
	                            const img = {
	                                url,
	                                originWidth: res.width,
	                                originHeight: res.height,
	                                localPath: url //res.path
	                            };
	                            this.cache[url] = img;
								/*
								uni.showModal({
								    title: '',
								    content: 'saveImageToLocal'+res.width
								});
								*/
	                            // console.log(res);
	                            // resolve(res.path)
	                            resolve(img);
	                        } else {
	                            reject(new Error('getImageInfo fail'));
	                        }
	                    }
	                });
	            } else {
	                this.cache[url] = {
	                    url,
	                    originWidth: 0,
	                    originHeight: 0,
	                    localPath: url
	                };
	                resolve(url);
	            }
	        }
	    })
	},
	saveImageToLocal: function () {
	    const { width, height } = this;
	    uni.canvasToTempFilePath({
	        x: 0,
	        y: 0,
	        width,
	        height,
	        canvasId: 'canvasdrawer',
	        success: res => {
	            if (res.errMsg === 'canvasToTempFilePath:ok') {
	                this.isPainting = false;
	                this.imageList = [];
	                this.tempFileList = [];
					this.shareImage = res.tempFilePath ;
					wx.hideLoading();
					/*
	                this.$emit('success', {
	                    width,
	                    height,
	                    path: res.tempFilePath
	                });
					*/
				   /*
				   uni.showModal({
				       title: '',
				       content: 'isPainting:'+this.isPainting+' shareImage:'+this.shareImage
				   });
				   */
	            }
	        },
			fail: err => {
				wx.hideLoading();
				uni.showModal({
				    title: '',
				    content: 'saveImageToLocal'+err
				});
				
			}
	    }, this);
	},
	
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
